

















export default {
	name:"m-footer-car",
	props:{
		title:{
			type:String,
			value:"",
		},
		price:{
			type:String,
			value:"",
		},
		num:{
			type:String,
			value:"",
		}
	},
	data() {
		return {
			animation:false
		};
	},
	watch:{
		num(){
			this.animation=true;
			let time = setTimeout(()=>{
				this.animation=false;
				clearTimeout(time)
			},100)
		}
	},
	methods:{
