








	export default {
		name: 'uni-number-box',
		props: {
			id: {
				type: [Number,String],
				default: 1
			},
			value: {
				type: Number,
				default: 1
			},
			
			min: {
				type: Number,
				default: 0
			},
			max: {
				type: Number,
				default: 100
			},
			step: {
				type: Number,
				default: 1
			},
			disabled: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				inputValue: this.value
			}
		},
		computed: {
			disableSubtract() {
				return this.inputValue <= this.min
			},
			disableAdd() {
				return this.inputValue >= this.max
			}
		},
		watch: {
			value(val) {
				console.log('改变',val);
				this.inputValue = val;
			},
// 			inputValue(val) {
// 				this.$emit('change', {num:val,id:this.id});
// 			}
		},
		methods: {
			//
			deleteFn(type){
				console.log('zzz');
				this.$emit('delete', {num:this.inputValue,id:this.id,type:type});
			},
			_calcValue(type) {
				if (this.disabled) {
					return
				}
				const scale = this._getDecimalScale()
				let value = this.inputValue * scale
				let step = this.step * scale
				if (type === 'subtract') {
					value -= step
				} else if (type === 'add') {
					value += step
				}
				if (value < this.min || value > this.max) {
					return
				}
				this.inputValue = value / scale;
				this.$emit('change', {num:this.inputValue,id:this.id,type:type});
				
			},
			_getDecimalScale() {
				let scale = 1
				// 浮点型
				if (~~this.step !== this.step) {
					scale = Math.pow(10, (this.step + '').split('.')[1].length)
				}
				return scale
			},
			_onBlur(event) {
				let value = event.detail.value
				if (!value) {
					this.inputValue = 0
					return
				}
				value = +value;
				if ((value*1) > (this.max*1)) {
					console.log('这里快点'+this.max);
					value = this.max
				} else if ((value*1) < (this.min*1)) {
					value = this.min
				}
				this.inputValue = value+"";
				this.$emit('change', {num:this.inputValue*1,id:this.id});
			}
		}
	}
