



































export default {
	name:"m-store-pro",
	props:{
		rowData:{
			type:Object,
			rowData:{}
		},
		pictureUrl:{
			type:[String,Number],
			default:""
		},
		isAssemble:{
			type:[String,Number],
			default:""
		},
		synopsis:{
			type:[String,Number],
			default:""
		},
		labelNames:{
			type:[String,Number],
			default:""
		},
		presentPrice:{
			type:[String,Number],
			default:""
		},
		originalPrice:{
			type:[String,Number],
			default:""
		},
		isadd:{
			type:[String,Number],
			default:""
		}
	},
	methods:{
		proDetail(e){
			this.$emit("proDetail",this.rowData)
		},
		touchOnGoods(e){
			 this.$emit("touchOnGoods",{
				 data:this.rowData,
				 elem : e,
			 })
		}
	},
	data() {
		return {
			isAdd:false
		};
	}
}
